
import { defineComponent, ref, onMounted, reactive, toRefs } from "vue";
import { exportExcel, exportZip } from "@/util/exportFile";
import { warningMsgBox } from "@/util/messageBox";
import $api from "@/api";

export default defineComponent({
    name: "ExportFile",
    setup() {
        let tableData = ref<any>([]);

        let loading = ref(false);

        let state = reactive({
            page: 1,
            size: 10,
            total: 0,
        });

        const pageChange = (e: number) => {
            state.page = e;
            getData();
        };
        const sizeChange = (e: number) => {
            state.size = e;
            getData();
        };

        let exportFileName = ref("");

        const exportTable = (type: number) => {
            warningMsgBox(`确定导出${type === 0 ? "Excel" : "Zip"}吗？`)
                .then(() => {
                    const header = ["id", "title", "price", "mack", "supplier", "img_url"];
                    const filterVal = ["_id", "title", "price", "mack", "supplier", "img_url"];
                    if (type === 0) {
                        exportExcel({
                            header,
                            data: tableData.value,
                            filterVal,
                            filename: exportFileName.value,
                        });
                    } else if (type === 1) {
                        exportZip({
                            header,
                            filterVal,
                            data: tableData.value,
                            filename: exportFileName.value,
                        });
                    }
                })
                .catch(() => {});
        };

        const getData = async () => {
            loading.value = true;
            const {
                data: { list, pageCount },
            } = await $api.goods.queryGoods({
                page: state.page,
                size: state.size,
            });
            tableData.value = list;
            state.total = pageCount;
            loading.value = false;
        };

        onMounted(() => {
            getData();
        });

        return {
            tableData,
            loading,
            ...toRefs(state),
            exportTable,
            exportFileName,
            pageChange,
            sizeChange,
        };
    },
});
